@import '../../../node_modules/bootstrap/dist/css/bootstrap.min.css';
html, body, div, span, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
abbr, address, cite, code,
del, dfn, em, img, ins, kbd, q, samp,
small, strong, sub, sup, var,
b, i,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, figcaption, figure,
footer, header, hgroup, menu, nav, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}

body {
  line-height: 1;
}

article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

nav ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

a {
  margin: 0;
  padding: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
  text-decoration: none;
}

a, a:hover, a:focus, a:active {
  text-decoration: none;
  color: inherit;
}

/* change colours to suit your needs */
ins {
  background-color: #ff9;
  color: #000;
  text-decoration: none;
}

/* change colours to suit your needs */
mark {
  background-color: #ff9;
  color: #000;
  font-style: italic;
  font-weight: bold;
}

del {
  text-decoration: line-through;
}

abbr[title], dfn[title] {
  border-bottom: 1px dotted;
  cursor: help;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* change border colour to suit your needs */
hr {
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid #cccccc;
  margin: 1em 0;
  padding: 0;
}

input, select {
  vertical-align: middle;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

a {
  color: inherit;
  text-transform: uppercase;
  text-decoration: none;
}

button {
  background-color: transparent;
  border: none;
  cursor: pointer;
  transition: 0.15s;
}

input {
  background-color: transparent;
  border: none;
}

li {
  list-style-type: none;
}

input:focus, textarea:focus, select:focus {
  outline: none;
}

.container {
  max-width: 1210px;
  margin: 0 auto;
  padding: 0 15px;
}

.header {
  font-family: "Rubik";
  font-weight: 400;
  font-size: 1rem;
  line-height: 1.185rem;
  background-color: #206A5D;
  color: #FFFFFF;
}
.header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__logo {
  margin: 15px 0;
}
.header__menu {
  flex-grow: 1;
}
.header__menu-list {
  height: 100%;
  display: flex;
  justify-content: space-around;
}
.header__menu-list-item {
  flex-grow: 1;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  padding: 18px 52px;
}
.header__menu-list-item:hover {
  background-color: #288776;
  transition: all 0.3s ease-in-out;
}
.header__button {
  color: #FFFFFF;
  background-color: #F58634;
  border-radius: 10px;
  padding: 18px 52px;
}
.header__button:hover {
  background-color: #288776;
  transition: all 0.3s ease-in-out;
}

.hero {
  font-family: "Rubik";
  font-weight: 400;
  font-size: 1rem;
  line-height: 1.185rem;
  background-color: #288776;
  color: #FFFFFF;
}
.hero__inner {
  position: relative;
}
.hero__title {
  color: #FFCC29;
  font-family: "CC SamaritanTallLower";
  font-weight: 400;
  font-size: 4rem;
  line-height: 3.6rem;
  letter-spacing: 0.2rem;
  padding: 104px 0 0 0;
}
.hero__title span {
  color: #81B214;
}
.hero__descr {
  width: 291px;
  padding: 137px 0 48px 0;
  font-weight: 400;
  font-size: 1.125rem;
  line-height: 1.2169rem;
}
.hero__button {
  color: #FFFFFF;
  background-color: #F58634;
  background-color: 16px;
  font-size: 16px;
  font-weight: 400;
  line-height: 16px;
  border-radius: 10px;
  padding: 18px 51px;
  margin: 0 0 230px 0;
}
.hero__button:hover {
  background-color: #206A5D;
  transition: all 0.3s ease-in-out;
}
.hero__background-svg {
  position: absolute;
  right: 0;
  bottom: 0;
}

.about-us {
  font-family: "Rubik";
  font-weight: 400;
  font-size: 1rem;
  line-height: 1.185rem;
  background-color: #FFFFFF;
  color: #FFFFFF;
}
.about-us__inner {
  display: flex;
  flex-direction: column;
}
.about-us__list {
  transform: translateY(-69px);
  display: flex;
  justify-content: space-between;
}
.about-us__list-item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 225px;
  width: 380px;
  color: #FFD654;
  background-color: #206A5D;
  border-radius: 10px;
}
.about-us__list-item-text {
  font-size: 1.125rem;
  line-height: 1.2306rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.about-us__list-item-text span:nth-child(1) {
  font-family: "CC SamaritanTallLower";
  font-weight: 400;
  font-size: 4rem;
  line-height: 4.3762rem;
  padding: 0 0 8px 0;
}
.about-us__descr {
  align-self: center;
  height: 210px;
  width: 580px;
  color: #206A5D;
  font-size: 1.125rem;
  line-height: 1.4063rem;
  padding: 124px 0 450px;
}
.about-us__descr p:not(:last-child) {
  padding: 0 0 33px 0;
}

.groups {
  font-family: "CC SamaritanTallLower";
  font-weight: 400;
  background-color: #E8F1F0;
  color: #FFFFFF;
}
.groups__inner {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.groups__title {
  color: #288776;
  padding: 41px 0 25px;
  font-size: 4rem;
  line-height: 5.2562rem;
}
.groups__cards {
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding: 0 0 100px 0;
}
.groups__cards-item {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 380px;
  height: 380px;
}
.groups__cards-item::after {
  content: "";
  width: 360px;
  height: 360px;
  background-color: #FFD654;
  position: absolute;
  bottom: 0;
  right: 0;
}
.groups__cards-item::before {
  content: "";
  width: 360px;
  height: 360px;
  background-color: #F58634;
  position: absolute;
  top: 0;
  left: 0;
}
.groups__cards-item-title {
  z-index: 1;
  color: #288776;
  font-size: 2.25rem;
  line-height: 2.9562rem;
  padding: 0 0 30px 0;
}
.groups__cards-item-button {
  z-index: 1;
  font-family: "Rubik";
  font-weight: 400;
  color: #FFFFFF;
  background-color: #F58634;
  background-color: 16px;
  font-size: 16px;
  font-weight: 400;
  line-height: 16px;
  border-radius: 10px;
  padding: 18px 51px;
}
.groups__cards-item-button:hover {
  background-color: #FFD654;
  transition: all 0.3s ease-in-out;
}

.squad {
  position: relative;
  font-family: "CC SamaritanTallLower";
  font-weight: 400;
  background-color: #FFFFFF;
  color: #FFFFFF;
}
.squad__inner {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}
.squad__title {
  color: #288776;
  padding: 251px 0 21px;
  font-size: 4rem;
  line-height: 5.2562rem;
}
.squad__descr {
  font-family: "Rubik";
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 580px;
  height: 280px;
  margin: 0 0 360px 0;
}
.squad__descr::after {
  content: "";
  width: 560px;
  height: 260px;
  background-color: #FFCC29;
  position: absolute;
  bottom: 0;
  right: 0;
}
.squad__descr::before {
  content: "";
  width: 560px;
  height: 260px;
  background-color: #FFD654;
  position: absolute;
  top: 0;
  left: 0;
}
.squad__descr-text {
  z-index: 1;
  color: #206A5D;
  padding: 68px 0 25px 58px;
}
.squad__descr-text span {
  font-weight: 700;
}
.squad__descr-button {
  z-index: 1;
  color: #FFFFFF;
  background-color: #F58634;
  background-color: 16px;
  font-size: 16px;
  font-weight: 400;
  line-height: 16px;
  border-radius: 10px;
  padding: 18px 51px;
}
.squad__descr-button:hover {
  background-color: #FFD654;
  transition: all 0.3s ease-in-out;
}
.squad__background-svg {
  position: absolute;
  top: -20px;
  width: 100%;
}
.squad__background-svg svg {
  width: 100%;
}
.squad__background-svg-tree {
  position: absolute;
  bottom: 0;
  left: 0;
}

.footer {
  font-family: "Rubik";
  font-weight: 400;
  background-color: #288776;
  color: #FFFFFF;
}
.footer__inner {
  display: flex;
  justify-content: center;
  align-items: center;
}
.footer__link {
  font-size: 0.9375rem;
  line-height: 1.1113rem;
  padding: 34px 0;
}